import React from "react"

const list = [];

class Item extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            checked: false
        }
    }

    checkIt(){
        this.setState(prevState => {
            if(!prevState.checked){
                return {checked: true}
            }
            else {
                return {checked: false}
            }
        })
    }
    render (){
    let style1 = {paddingLeft:20}
    if(this.state.checked){
        style1.textDecoration = "line-through"
        style1.color = "gray"
    }
    

    return (
        <div className="finalpoint">
            <input type="checkbox" onChange={this.checkIt.bind(this)}/>
            <span style = {style1}>{this.props.item}</span>
            <hr></hr>
        </div>
        
    )
}
}


function List() {
    
    const todoitems = list.map(e => <Item key = {list.indexOf(e)} item = {e} />)
    const oops = <p style={{textAlign: "center"}}>Oops..! There's nothing here.<br></br> Add things to show.</p>
    return(
        <div>
            { list.length===0 ? oops : todoitems }
        </div>
    )
}

class AddItems extends React.Component {
    
    constructor(props){
        super(props)
        this.state = {
            add: "",
            submit: false
        }
    }

    handleChange(event){
        this.setState({
            add : event.target.value,
            submit: false
        })
    }

    addList(){
        
        this.setState(prevState => {
            return {
                submit: true
            }
        })
    }
    
    
    render(){    
        if (this.state.submit){
            list.push(this.state.add)
        }
        return(
            <div className="itemsbox">
                <input type="text" className="inn" name="inn" placeholder="Enter things to do" onChange={this.handleChange.bind(this)}/>
                <button onClick={this.addList.bind(this)}>Add</button>
            </div>
            
        )
    }
}


function ToDoItems(props) {
    console.log(props.yo)
    let rende
    if (props.yo==="main"){
        rende = <div className="itemsbox">
                        <List/>
                    </div>
    }
    else {
        rende = <AddItems />
    }
    return(
        rende
    )
}


export default ToDoItems