import React from "react"
import ToDoItems from "./ToDoItems"



function Footer(){
    return (
        <footer>Â© The_Hyperboy</footer>
    )
}

class App extends React.Component {
    constructor(){
        super()
        this.state = {
            type: "main"
        }
    }

    handleClick() {
        
        this.setState(prevState => {
            if (prevState.type === "main"){
                return {
                    type: "add"
                }
            }
            else {
                return {
                    type: "main"
                }
            }
        })
        
    }

    render () {

        return(
            <div className="full">
                <nav className="navbar">
                    <h1 className="logo">To-Do-List</h1>
        <p onClick={this.handleClick.bind(this)}>{this.state.type==="main" ? "Add items" : "Show To-Do-List"}</p>
                </nav>
                {this.state.type === "main" ? 
                <ToDoItems yo="main"/> : <ToDoItems yo="add"/>}
                <div className="footer">
                    <Footer />
                </div>
                <br></br>
                <br></br>
            </div>
        )
    }
}

export default App 